function buildDistanceInWordsLocale () {
  var distanceInWordsLocale = {
    lessThanXSeconds: {
      one: '   ',
      other: '  {{count}} '
    },

    xSeconds: {
      one: ' ',
      other: '{{count}} '
    },

    halfAMinute: ' ',

    lessThanXMinutes: {
      one: '  ',
      other: '  {{count}} '
    },

    xMinutes: {
      one: ' ',
      other: '{{count}} '
    },

    aboutXHours: {
      one: '  ',
      other: '{{count}}  '
    },

    xHours: {
      one: ' ',
      other: '{{count}} '
    },

    xDays: {
      one: ' ',
      other: '{{count}} '
    },

    aboutXMonths: {
      one: '  ',
      other: '{{count}}  '
    },

    xMonths: {
      one: ' ',
      other: '{{count}} '
    },

    aboutXYears: {
      one: '  ',
      other: '{{count}}  '
    },

    xYears: {
      one: ' ',
      other: '{{count}} '
    },

    overXYears: {
      one: '  ',
      other: '  {{count}} '
    },

    almostXYears: {
      one: '  ',
      other: '{{count}}  '
    }
  }

  function localize (token, count, options) {
    options = options || {}

    var result
    if (typeof distanceInWordsLocale[token] === 'string') {
      result = distanceInWordsLocale[token]
    } else if (count === 1) {
      result = distanceInWordsLocale[token].one
    } else {
      result = distanceInWordsLocale[token].other.replace('{{count}}', count)
    }

    if (options.addSuffix) {
      if (options.comparison > 0) {
        return '  ' + result
      } else {
        return ' ' + result
      }
    }

    return result
  }

  return {
    localize: localize
  }
}

module.exports = buildDistanceInWordsLocale
